* {
  margin: 0;
  padding: 0;
}

.fruits {
  width: 500px;
  margin: 0 auto;
  margin-top: 10px;
}

.btns {
  max-width: 900px;
  margin: 0 auto;
}

.btns button {
  border: 1px solid #bdc3c7;
  padding: 5px 10px;
  cursor: pointer;
  border-radius: 6px;
  margin-right: 6px;
  margin-top: 10px;
}
.fruits button:hover {
  border-color: #2980b9;
}

.fruits ul {
  list-style: none;
}

.fruits ul li {
  padding: 6px 20px;
  margin-bottom: 5px;
  background: #eee;
  border-radius: 6px;
  cursor: pointer;
  word-spacing: 20px;
  box-sizing: border-box;
}

.fruits ul li img {
}

.fruits ul li.always-display-style {
  border-left: 5px solid #9b59b6;
}

.fruits ul li:hover {
  background: #e5e5e5;
}

span.html-tag {
  color: #10b981;
  font-weight: bold;
}

span.purple {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-color: #9b59b6;
}

p.description {
  margin: 10px 0;
}

img.fruit-img {
  height: 20px;
}

h1 {
  text-align: center;
}
